import './sass/styles.scss';
import Swiper from 'swiper';

import {MDCTopAppBar} from '@material/top-app-bar/index';
import {MDCRipple} from '@material/ripple/index';
import {MDCLineRipple} from '@material/line-ripple/index';
import {MDCList} from '@material/list/index';
import {MDCMenuSurface} from '@material/menu-surface';
import {MDCMenu} from '@material/menu';


window.onload = () => {

    let ThemeScript = {};

    /**
     * get menu button
     * @type {HTMLElement}
     */
    const menuBtn = document.getElementById("topAppButton");

    /**
     * Body element
     * @type {HTMLElement}
     */

    const body = document.body;

    /**
     * Dropdown element
     * @type {HTMLElement}
     */
    const menuDrop = document.getElementById('collapseMenu');

    /**
     * get Top app bar
     * @type {Element}
     */
    const mdcTopAppBar = document.getElementById('topAppBar');

    /**
     *
     * @type {NodeListOf<Element>}
     */
    const tabLinks = document.querySelectorAll('.ac-office-tour__link');
    const galleries = document.querySelectorAll('.ac-office-tour__content');

    /**
     * get filter section group
     * @type {Element}
     */
    const filterElements = document.querySelectorAll('.filter');
    const filterGroup = document.querySelector('.filter-section__group');


    const tabContainer = document.getElementById('tab');
    const accordions = document.querySelectorAll('.accordion__group');

    const careersItems = document.querySelectorAll('.career');

    /**
     *
     * @type {{construct: construct}}
     */
    ThemeScript = {
        construct: function () {
            ThemeScript.materialConfig();
            ThemeScript.checkDocumentScrollPosition();
            ThemeScript.headerInteractiveOnScroll();
            ThemeScript.expandMenu();
            ThemeScript.puppySlider();
            ThemeScript.trainingSlider();
            ThemeScript.dropdownAction();
            ThemeScript.filterActions();
            ThemeScript.removeMultipleFilter();
            ThemeScript.removeFilterActionsOnBodyClick();
            ThemeScript.applySelection();
            if (tabContainer) {
                ThemeScript.tabFunction();
            }
            if (ThemeScript.isMobile()) {
                ThemeScript.filterActionsMobile();
            }
            if (accordions.length > 1) {
                ThemeScript.accordionFunctions();
            }
            if (careersItems.length > 0) {
                ThemeScript.careerAccordionFunctions();
            }

        },
        isMobile: () => {
            return window.screen.width < 992;
        },
        materialConfig() {
            const mdcButton = document.querySelectorAll('.mdc-button');
            const mdcLineRipple = document.querySelector('.mdc-line-ripple');
            const mdcList = document.querySelector('.mdc-list');
            const mdcMenuSurface = document.querySelectorAll('.mdc-menu-surface');
            const mdcMenu = document.querySelectorAll('.mdc-menu');
            const mdcMenuList = document.querySelectorAll('.mdc-list--stop-propagation .mdc-list-item');

            if (mdcButton) {
                Array.prototype.forEach.call(mdcButton, (item) => {
                    let ripple = new MDCRipple(item);
                });
            }
            if (mdcLineRipple) {
                const lineRipple = new MDCLineRipple(mdcLineRipple);
            }
            if (mdcTopAppBar) {
                const topAppBar = new MDCTopAppBar(mdcTopAppBar);
            }
            if (mdcList) {
                let list = new MDCList(mdcList);
                const listItemRipples = list.listElements.map((mdcList) => new MDCRipple(mdcList));
            }
            if (mdcMenuSurface.length > 0) {
                Array.prototype.forEach.call(mdcMenuSurface, (item) => {
                    let menuSurface = new MDCMenuSurface(item);
                });
            }
            if (mdcMenu.length > 0) {
                Array.prototype.forEach.call(mdcMenuSurface, (item) => {
                    let menu = new MDCMenu(item);
                });
            }
            if (mdcMenuList.length > 0) {
                Array.prototype.forEach.call(mdcMenuList, (item) => {
                    item.addEventListener('click', (event) => {
                        event.stopPropagation();
                    });
                });
            }
        },
        checkDocumentScrollPosition: () => {
            if (window.scrollY === 0) {
                mdcTopAppBar.classList.add('main-header--on-top');
            } else {
                mdcTopAppBar.classList.remove('main-header--on-top');
            }
        },
        headerInteractiveOnScroll: () => {
            document.addEventListener('scroll', () => {
                ThemeScript.checkDocumentScrollPosition();
            }, false);
        },
        expandMenu: () => {
            if (menuBtn) {
                menuBtn.onclick = () => {
                    ThemeScript.toggleMenu();
                };
            }
        },
        toggleMenu() {
            body.style.overflow = body.style.overflow === 'hidden' ? 'initial' : 'hidden';
            body.style.position = body.style.position === 'fixed' ? 'initial' : 'fixed';
            mdcTopAppBar.classList.toggle('main-header--nav-open');
        },
        /**
         * * Slider Functions
         * - puppySlider();
         * - trainingSlider();
         *
         * */
        puppySlider() {
            const galleryThumbs = new Swiper('.puppy__gallery-thumbs', {
                spaceBetween: 8,
                slidesPerView: 3,
                freeMode: true,
                watchSlidesVisibility: true,
                watchSlidesProgress: true,
            });
            const puppySlider = new Swiper('.puppy__slider', {
                spaceBetween: 10,
                navigation: {
                    nextEl: '.puppy__arrow--next',
                    prevEl: '.puppy__arrow--prev',
                },
                thumbs: {
                    swiper: galleryThumbs
                },
            });
        },
        trainingSlider: () => {
            const trainingSlider = new Swiper('.training--slider', {
                loop: true,
                navigation: {
                    nextEl: '.training__next',
                },
                pagination: {
                    el: '.training__pagination',
                    clickable: true,
                },
            });
        },
        dropdownAction() {
            const btnCollapse = document.getElementById('filterMenu');

            if (btnCollapse && menuDrop) {
                btnCollapse.addEventListener('click', (e) => {
                    ThemeScript.expandDropdown();
                    e.stopPropagation();
                });
                body.addEventListener('click', () => {
                    if (!menuDrop.classList.contains('menu__collapse--collapsed')) {
                        ThemeScript.expandDropdown();
                    }
                });
            }
        },
        expandDropdown() {
            menuDrop.classList.toggle('menu__collapse--collapsed');
            body.classList.toggle('menu-collapse-is-open');
        },
        toggleBodyOverlay() {
            body.classList.toggle('on-overlay');
        },
        /**
         * * Start Functions for theme tabs
         * - filterActions();
         * - removeFilterActionsOnBodyClick();
         *
         */
        filterActions() {
            if (filterElements) {
                Array.prototype.forEach.call(filterElements, filter => {
                    const btn = filter.querySelector('.filter-button');
                    btn.addEventListener('click', (e) => {
                        if (filter.classList.contains('filter--open')) {
                            ThemeScript.removeFiltersActiveClassName(filterElements);
                        } else {
                            ThemeScript.removeFiltersActiveClassName(filterElements);
                            filter.classList.add('filter--open');
                        }
                    });
                })
            }
        },
        removeFilterActionsOnBodyClick() {
            if (filterElements) {
                body.addEventListener('click', (e) => {
                    Array.prototype.forEach.call(filterElements, filter => {
                        if (!filter.contains(e.target)) {
                            filter.classList.remove('filter--open');
                        }
                    });
                });
            }
        },
        filterActionsMobile() {
            const btnsMobile = document.querySelectorAll('.filter-section__mobile-group > button');

            if (btnsMobile.length > 0) {
                Array.prototype.forEach.call(btnsMobile, btn => {
                    btn.addEventListener('click', (e) => {
                        const element = document.getElementById(btn.dataset.target);
                        filterGroup.classList.add('filter-section__group--open');
                        element.classList.add('filter--open');
                        ThemeScript.toggleBodyOverlay();
                        e.stopPropagation();
                    });
                })
            }
        }
        ,
        removeMultipleFilter() {
            if (filterGroup) {
                body.addEventListener('click', (e) => {
                    if (filterGroup.classList.contains('filter-section__group--open')
                            && !filterGroup.contains(e.target)) {
                        filterGroup.classList.remove('filter-section__group--open');
                        ThemeScript.toggleBodyOverlay();
                    }
                });
            }
        }
        ,
        removeFiltersActiveClassName(filters) {
            Array.prototype.forEach.call(filters, filter => {
                filter.classList.remove('filter--open');
            });
        },
        applySelection() {
            const btn = document.querySelector('.filter-section__buttons .mdc-button--apply');

            if (btn) {
                btn.addEventListener('click', () => {
                    ThemeScript.toggleBodyOverlay();
                    filterGroup.classList.remove('filter-section__group--open');
                } )
            }
        },
        /**
         * * Start Functions for theme tabs
         * - tabFunction();
         * - removeTabsActiveClass();
         *
         */
        tabFunction() {
            const tabButtons = tabContainer.querySelectorAll('.tab__button');

            Array.prototype.forEach.call(tabButtons, btn => {
                btn.addEventListener('click', e => {
                    ThemeScript.removeTabsActiveClass();
                    const activeTab = tabContainer.querySelector(`#${btn.dataset.target}`);
                    btn.classList.add('tab__button--active');
                    activeTab.classList.add('tab__box--active')
                });
            });
        },
        removeTabsActiveClass() {
            const tabs = tabContainer.querySelectorAll('.tab__box');
            const tabButtons = tabContainer.querySelectorAll('.tab__button');
            Array.prototype.forEach.call(tabButtons, btn => {
                if (btn.classList.contains('tab__button--active')) {
                    btn.classList.remove('tab__button--active');
                }
            });
            Array.prototype.forEach.call(tabs, tab => {
                if (tab.classList.contains('tab__box--active')) {
                    tab.classList.remove('tab__box--active');
                }
            });
        },
        /**
         * * Start Functions for theme accordion
         * - accordionFunctions();
         * - removeAccordionsActiveClass();
         *
         */
        accordionFunctions: () => {

            Array.prototype.forEach.call(accordions, accordion => {
                const accBtn = accordion.querySelector('.accordion__btn');

                accBtn.addEventListener('click', () => {
                    if (!accordion.classList.contains('accordion__group--active')) {
                        ThemeScript.removeAccordionsActiveClass();
                        accordion.classList.add('accordion__group--active');
                    }
                });
            });
        },
        removeAccordionsActiveClass: () => {
            Array.prototype.forEach.call(accordions, accordion => {
                accordion.classList.remove('accordion__group--active');
            });
        },
        /**
         * * Start Functions for theme accordion Careers
         * - accordionFunctions();
         * - removeAccordionsActiveClass();
         *
         */
        careerAccordionFunctions: () => {
            Array.prototype.forEach.call(careersItems, item => {
                const btn = item.querySelector('.career__drop');

                btn.addEventListener('click', () => {
                    if (!item.classList.contains('career--active')) {
                        ThemeScript.removeCareerActiveClass();
                        item.classList.add('career--active');
                    } else {
                        item.classList.remove('career--active');
                    }
                })
            });
        },
        removeCareerActiveClass: () => {
            Array.prototype.forEach.call(careersItems, item => {
                item.classList.remove('career--active');
            });
        },
    };

    ThemeScript.construct();
}
;